import React from 'react'
import { AboutContainer, AboutSection, Article, ArticleBody, ArticleTitle, Image, ImageBox } from '../../styles/about/About'

const About = () => {

  const article = {
    body: `
    My names Ron, Im 34, have worked as a software engineer for a smaller company, in an agile team, working mostly on online maps, with Java and C++. But just like many other startups this one eventually hasnt taken of, and now I find myself unemployed. I prefer to work in a smaller team, and see the direct impact I have on a project, which is the reason no. 1 why I am applying for the job with you. Other than that I am more or less an ordinary guy, with a girlfriend and a dog, and a passion for model trains. Is there anything else youd like to know about my life at the moment?
    `
  }

  return (
    <AboutSection id="about">
      <AboutContainer>
        <ImageBox>
          <Image src='/images/main.jpg' alt='image' />
        </ImageBox>
        <Article>
          <ArticleTitle>
            About<span>Me</span>
          </ArticleTitle>
          <ArticleBody>
            {article.body}
          </ArticleBody>
        </Article>
      </AboutContainer>
    </AboutSection>
  )
}

export default About